body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

table {
  margin-bottom: 2em;
}

td {
  width: 1.5em;
  text-align: center;
}

tbody td {
  font-size: 12px;
  width: 2em;
  line-height: 2em;
}

thead tr.header-name td {
  background-color: #EDF8CB !important;
}


tbody tr td:nth-child(1){

  background-color: #FCF90F;
}
tbody tr td:nth-child(7){
  background-color: #FCF90F;
}

tbody tr td {
  background-color: #C1D98E;
  color: #253716;
}
tbody tr td.invalid{
  background-color: #ccc !important;
}




